.page {
  background: #ffffff;
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.04);
  border-radius: 20px;
  max-width: 1080px;
  margin: 65px auto;
  padding-top: 45px;
  padding-bottom: 60px;
  @media (max-width: 1079px) {
    margin: 0 auto;
    padding: 0;
  }
  .header {
    display: flex;
    justify-content: space-between;
    padding-top: 40px;
    padding-bottom: 40px;
    margin: 0;
    padding-left: 60px;
    padding-right: 60px;
    align-items: center;
    @media (max-width: 710px) {
      flex-direction: column;
      align-items: center;
      padding: 30px;
      gap: 15px 0;
    }
  }
  .title {
    font-weight: 700;
    font-size: 32px;
    line-height: 39px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    margin: 0;
    padding-right: 50px;
    @media (max-width: 710px) {
      display: none;
    }
  }
}
